.TodoSearch{
    background:#e4eff5;
    border-radius:5rem;
    border:0.2rem solid #202329;
    height:6rem;
    width: 59%;
    font-size: 2rem;
    text-align:center;
    font-weight:700;
    color:#1e1e1f;
    box-shadow:0 0.5rem 5rem rgba(32,35,41,.25);
    position: absolute;
    margin-top: 10rem;
}
.TodoSearch::placeholder{
    color:#a5a5a5;
    font-weight:400
}
.TodoSearch:focus{
    outline-color:#61dafa
}

.TodoSearch:disabled{
    opacity: 25%;
}

@media (max-width: 1024px) {
    .TodoSearch{
        width: 98%;
        display: flex;
        align-items: center;
        justify-content: center;
    }
}